import React, { useState } from 'react';

const ImageWrapper = ({ source }) => {
    let [hovered, setHovered] = useState(false)
    return (
        <img src={source} alt="Girl in a jacket"
            className="grid-item transition duration-500 ease-in-out transform"
            key={source}
            onMouseEnter={() => setHovered(true)}
            onMouseLeave={() => setHovered(false)}
            style={{ transform: `${hovered ? 'scale(1.2,1.2)' : 'scale(1.0,1.0)'}`, zIndex: `${hovered ? '99' : '0'}` }}

        />
    );
}

export default ImageWrapper;